<template>
<div>
  <el-row style="margin: 18px 0px 0px 18px">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{path: '/admin'}">管理中心</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户信息</el-breadcrumb-item>
    </el-breadcrumb>
  </el-row>
<!--  备用一行,用来注册用户-->
  <el-card style="margin: 18px 2%;width: 95%">
    <el-table
    :data="users"
    stripe
    style="width: 100%"
    :max-height="tableHeight">
      <el-table-column
        type="selection"
        width="55"
        show-overflow-tooltip></el-table-column>
      <el-table-column
      prop="id"
      label="id"
      width="100"></el-table-column>
      <el-table-column
      prop="username"
      label="用户名"
      fit></el-table-column>
      <el-table-column
        prop="name"
        label="真实姓名"
        fit></el-table-column>
      <el-table-column
        prop="phone"
        label="手机号"
        fit></el-table-column>
      <el-table-column
        prop="email"
        label="邮箱"
        fit></el-table-column>
      <el-table-column
      prop="sta"
      label="状态"
      width="100">
        <div slot-scope="scope">
          <el-switch
          v-model="scope.row.enabled"
          active-color="#13ce66"
          inactive-color="#ff4949"></el-switch>
        </div>
      </el-table-column>
    </el-table>
  </el-card>
</div>
</template>

<script>
export default {
  name: 'UserProfile',
  data () {
    return {
      users: []
    }
  },
  computed: {
    tableHeight () {
      return window.innerHeight - 320
    }
  },
  mounted () {
    this.listUsers()
  },
  methods: {
    listUsers () {
      var _this = this
      this.$axios.get('/admin/user')
        .then(res => {
          if (res && res.status === 200) {
            _this.users = res.data
          }
        })
    }
  }
}
</script>

<style scoped>

</style>



